//https://blog.bam.tech/developer-news/4-ways-to-dispatch-actions-with-redux
class SceneApp extends Component {
    constructor(props) {
        super(props);
        this.state = {
            date: new Date(),
            modalVisible: false,
            panelVisible: false,
            duration: 1,
            sceneProgram: {},
            tool: {}
        };
        this._doRegisterPlay = this._doRegisterPlay.bind(this);
        this._doSave = this._doSave.bind(this);
        this._doPatternConfirm = this._doPatternConfirm.bind(this);
    }


    _doRegisterPlay(o) {
        this.refs.innerPlayer.doPlayScene(o.videoTime);
    }

    _doSave() {
        let time = this.refs.innerPlayer.getCurrentTime();
        if (!time) {
            time = 0;
        }
        this.refs.innerPlayer._doPlay();
        let aux = {moment: time};
        store.dispatch(saveRegister(aux));
        this._doPatternDismiss();
        showSuccess("Escena guardada"
            , <div><p>Se ha guardado el episodio de observación {UserSeconds(time)} </p></div>);
    }

    _doPatternSave(model) {
        if (model.startTime > model.endTime) {
            showError("Programación de escenas"
                , <div><p>El tiempo de finalización es menor que el tiempo de inicio.</p>
                    <p>Corrige tu planificación.</p></div>);
        } else {
            this.setState({modalVisible: true, sceneProgram: model});
        }
    }

    _doPatternConfirm() {
        const {startTime, endTime, time} = this.state.sceneProgram;
        if (startTime < endTime) {
            let maxScenes = Math.round((endTime - startTime) / time);
            for (let i = 0; i < maxScenes; i++) {
                const currentTime = startTime + (i * time);
                let scene = {moment: currentTime, name: "Auto-scene " + i};
                store.dispatch(saveRegister(scene));
            }
            showSuccess("Programación de escenas", `Se han creado ${maxScenes} escenas`);
        }
        this._doPatternDismiss();
    }

    _doPatternDismiss() {
        this.setState({modalVisible: false, sceneProgram: {}});
    }

    _showDrawer = () => {
        this.setState({
            panelVisible: true,
        });
    };
    _onCloseDrawer = () => {
        this.setState({
            panelVisible: false,
        });
    };
    _getSchema = () => {
        const duration = (this.refs.innerPlayer) ? this.refs.innerPlayer.getVideoLength() : 60;
        //this.setState({duration: 200});
        return FastSchemeValidator({
            title: 'Scene program',
            type: 'object',
            properties: {
                time: {
                    label: 'Scene gap (s)',
                    type: 'integer',
                    default: 5,
                    min: 5,
                    max: duration,
                    uniforms: {
                        component: TimeField
                    }
                },
                startTime: {
                    label: 'From (s)',
                    type: 'integer',
                    default: 5,
                    min: 0,
                    max: duration,
                    uniforms: {
                        component: TimeField
                    }
                },
                endTime: {
                    label: 'To (s)',
                    type: 'integer',
                    default: 5,
                    min: 1,
                    max: duration,
                    uniforms: {
                        component: TimeField
                    }
                }
            }
        })
    }

    render() {
        const currentScheme = this._getSchema();
        let modalMessage = "";
        if (this.state.sceneProgram) {
            const {startTime, endTime, time} = this.state.sceneProgram;
            modalMessage = <div> Se van a generar {Math.round((endTime - startTime) / time)} escenas.
                <p>¿Estás seguro de añadir estas escenas
                    desde {UserSeconds(startTime)} hasta {UserSeconds(endTime)}&#160;
                    cada {UserSeconds(time)}?</p>
            </div>;
        }
        return (
            <Provider store={store}>
                <StyledLayoutContentWrapper style={{height: '100vh'}}>
                    <LinceLayoutContent>
                        <PageHeader><IntlMessages id="scenes"/></PageHeader>
                        <Row>
                            <Col md={12}>
                                <LinceVideoPlayer ref="innerPlayer" defaultSize={24}/>
                            </Col>
                            <Col md={12}>
                                <Row>
                                    <CenteredCol md={24}>
                                        <Button type="primary" onClick={() => this._doSave()}>
                                            <IntlMessages id="save-current-position"/>
                                        </Button>&#160;
                                        <Button onClick={() => this._showDrawer()}>
                                            <FontAwesomeIcon icon={faClipboardList}/>
                                            <span>&#160;<IntlMessages id="schedule-scenes"/></span>
                                        </Button>
                                    </CenteredCol>
                                </Row>
                                <Row>
                                    <Col md={24}>
                                        <LinceRegister ref="register"
                                                       fullMode={false}
                                                       onPlayRegister={this._doRegisterPlay}/>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                        <Modal centered title={<IntlMessages id="accept-automatic-generation"/>}
                               visible={this.state.modalVisible}
                               onOk={() => this._doPatternConfirm()} onCancel={() => this._doPatternDismiss()}>
                            <p>{modalMessage}</p>
                        </Modal>
                        <Drawer title={<IntlMessages id="schedule-scenes"/>} width={520}
                                closable={true} onClose={this._onCloseDrawer} visible={this.state.panelVisible}>
                            <AutoForm schema={currentScheme} onSubmit={model => this._doPatternSave(model)}/>
                        </Drawer>
                    </LinceLayoutContent>
                </StyledLayoutContentWrapper>
            </Provider>
        );
    }
}

export default SceneApp;