import React, {Component} from 'react';
import PropTypes from "prop-types";
import {Trans} from 'react-i18next';
import {Menu} from 'antd';
import {faBellSlash, faFilm} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import imgKeys from "../../assets/images/LinceKeys.jpg";
import ShowNotification from "../utility/ShowNotification";
import LinceMenu from "../../containers/Lince/styled/LinceItems";

const {SubMenu} = Menu;

class LinceVideoManager extends Component {
    constructor(props) {
        super(props);
        this.onSoundTo = this.props.onSoundTo.bind(this);
        this.onResize = this.props.onResize.bind(this);
        this.onSetPlayBack = this.props.onSetPlayBack.bind(this);
        this.onStop = this.props.onStop.bind(this);
        this.onPause = this.props.onPause.bind(this);
        this.onSync = this.props.onSync.bind(this);
        this.onPlay = this.props.onPlay.bind(this);
        this.state = {speed: "speed:10", video: "view:2"}
    }

    handleClick = e => {
        switch (e.key) {
            case "play":
                this.onPlay();
                break;
            case "pause":
                this.onPause();
                break;
            case "stop":
                this.onStop();
                break;
            case "mute":
                this.onSoundTo();
                break;
            case "sync"                :
                this.onSync();
                break;
            case "speed:02":
                this.onSetPlayBack(0.2);
                this.setState({speed: e.key});
                break;
            case "speed:05":
                this.onSetPlayBack(0.5);
                this.setState({speed: e.key});
                break;
            case "speed:10":
                this.onSetPlayBack(1);
                this.setState({speed: e.key});
                break;
            case "speed:20":
                this.onSetPlayBack(2);
                this.setState({speed: e.key});
                break;
            case "view:1":
                this.onResize(24);
                this.setState({video: e.key});
                break;
            case "view:2":
                this.setState({video: e.key});
                this.onResize(12);
                break;
            case "view:3":
                this.setState({video: e.key});
                this.onResize(8);
                break;
            default:
                const message = <>
                    <div>Press this keys and experience with lince player in a new way:</div>
                    <ul>
                        <li><b>q</b>: Play</li>
                        <li><b>w</b>: Pause</li>
                        <li><b>e</b>: Stop</li>
                        <li><b>a</b>: Speed to 1x</li>
                        <li><b>s</b>: Speed to 0.2x</li>
                        <li><b>d</b>: Jump back 5 secs</li>
                        <li><b>f</b>: Jump forward 5 secs</li>
                        <li><b>1</b>: Change view to 1 video per row</li>
                        <li><b>2</b>: Change view to 2 video per row</li>
                        <li><b>3</b>: Change view to 3 video per row</li>
                        <li><b>g</b>: Save Scene to Register</li>
                    </ul>
                    <img src={imgKeys} alt="lince keys" width="100%"/>
                </>;
                ShowNotification("Keyboard shortcuts for lince player", message);
                break;
        }
        this.setState({
            current: e.key,
        });
        return false;
    };

    render() {
        return (
            <LinceMenu onClick={this.handleClick} mode="horizontal"
                       className={"font-green-sharp"}
                       ref={"linceMenu"}
                       theme={"dark"}
                       selectedKeys={[this.state.speed, this.state.video, this.state.current]}>
                <Menu.Item key="shortcuts" selectable={"false"}>
                    <b>
                        <Trans i18nKey="video.synchronizedActions" className={"isoWidgetLabel"}>Synchronized
                            actions</Trans>&#160;
                        <i className="ion-information-circled"/>
                    </b>
                </Menu.Item>
                <Menu.Item key="play">
                    <i className="ion-play"/>
                </Menu.Item>
                <Menu.Item key="pause">
                    <i className="ion-pause"/>
                </Menu.Item>
                <Menu.Item key="stop">
                    <i className="ion-stop"/>
                </Menu.Item>
                <SubMenu title={<i className="ion-clock"/>}>
                    <Menu.Item key="speed:02">0.2 x</Menu.Item>
                    <Menu.Item key="speed:05">0.5 x</Menu.Item>
                    <Menu.Item key="speed:10">1 x</Menu.Item>
                    <Menu.Item key="speed:20">2 x</Menu.Item>
                </SubMenu>
                <Menu.Item key="mute">
                    <FontAwesomeIcon icon={faBellSlash}/>&#160;
                    <span>MUTE</span>
                </Menu.Item>
                <Menu.Item key="sync">
                    <i className="ion-refresh"/>&#160;
                    <span>SINC</span>
                </Menu.Item>
                <SubMenu title={<FontAwesomeIcon icon={faFilm}/>}>
                    <Menu.Item key="view:1">1 video per row</Menu.Item>
                    <Menu.Item key="view:2">2 video per row</Menu.Item>
                    <Menu.Item key="view:3">3 video per row</Menu.Item>
                </SubMenu>
            </LinceMenu>

        )
            ;
    }
}

LinceVideoManager.propTypes = {
    onSoundTo: PropTypes.func,
    onSync: PropTypes.func,
    onSetPlayBack: PropTypes.func,
    onStop: PropTypes.func,
    onPause: PropTypes.func,
    onPlay: PropTypes.func,
    onResize: PropTypes.func,
};
export default LinceVideoManager;
