import React, {useState} from 'react';
import {connectField} from 'uniforms';
import {Col, InputNumber, Row, Slider} from 'antd';
import UserSeconds from "../../../utility/UserSeconds";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faStopwatch} from "@fortawesome/free-solid-svg-icons";

//check lince-plus/node_modules/uniforms-antd/src/NumField.tsx
const noneIfNaN = x => (isNaN(x) ? undefined : x);

function TimeField(props) {
    const [currentVal, setCurrentVal] = useState(false);
    let onSlideChange = (newValue) => {
        props.onChange(noneIfNaN(newValue));
        setCurrentVal(newValue);
    }
    return (
        <Row style={{marginBottom: "12px"}} className={" ant-form-item"}>
            <Col span={7} className="ant-form-item-label">
                <label htmlFor="" className="" title="">
                    <span><FontAwesomeIcon icon={faStopwatch}/>&#160;{props.label}</span>
                    <br/>
                    <span>{UserSeconds(currentVal)}</span>
                </label>
            </Col>
            <Col span={5}>
                <InputNumber
                    disabled={props.disabled}
                    id={props.id}
                    max={props.max}
                    min={props.min}
                    name={props.name}
                    style={{margin: '0 -15px'}}
                    value={currentVal} onChange={(v) => onSlideChange(v)}/>
            </Col>
            <Col span={12}>
                <Slider
                    max={props.max}
                    min={props.min}
                    onChange={(v) => onSlideChange(v)}
                    value={typeof currentVal === 'number' ? currentVal : 0}
                />
            </Col>

        </Row>

    );
}

export default connectField(TimeField);

